.navbarLink a {
    color:white;
    padding-left: 45px;
}

.active{
    font-weight: bolder;
    color: coral !important;
}

.navbarLink h5 { 
    color: white ;
}

.navbarLink a:hover , .footerLink a:hover {
    color:bisque;
    text-decoration: none;
    font-weight: bold;
}

.footerLink a {
    color: white ;
}

.Footer {
    position:relative;
    bottom: 0;
    width:100%;
    color: white;
    height: 257px;
    background-color: #343a40;
}

.ContentSection {
    min-height: calc(100vh - 388px); 
}

@media screen and (max-width: 768px) {
    .Content-Section {
        min-height: calc(100vh - 450px); 
    }
    .Footer {
        height:450px;
    }
}